.primary-layout {
  height: 100%;
}
.primary-layout .primary-sider {
  height: 100%;
}
.primary-layout .primary-header {
  height: 64px;
  background-color: #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.primary-layout .primary-header .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
  float: left;
}
.primary-layout .primary-header .trigger:hover {
  color: #1890ff;
}
.primary-layout .primary-header .ant-breadcrumb {
  margin-top: 19px;
  float: left;
}
.primary-layout .primary-header .user-info {
  float: right;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: url(data:image/jpeg;base64,);
  background-size: cover;
  margin-top: 12px;
  margin-right: 20px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.primary-layout .primary-content {
  height: calc(100% - 64px);
  overflow-y: auto;
  overflow-x: hidden;
  padding: 10px;
}
.login-layout {
  height: 100%;
  position: relative;
}
.login-layout .login-background {
  height: 100% !important;
  background-size: cover;
}
.login-layout .login-form {
  position: absolute;
  width: 320px;
  right: 100px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 5px;
  background-color: rgba(30, 148, 255, 0.1);
  box-shadow: 0 0 10px #1e94ff;
  padding: 20px;
  padding-bottom: 0;
}
.login-layout .login-form .title {
  font-size: 30px;
  line-height: 30px;
  margin-bottom: 20px;
  font-weight: bold;
  text-align: center;
  color: #1890ff;
  text-shadow: 0 0 5px rgba(30, 148, 255, 0.6);
}
.login-layout .login-form .ant-input-affix-wrapper {
  background-color: transparent !important;
  border: 1px solid #1890ff;
}
.login-layout .login-form .ant-input-affix-wrapper input.ant-input {
  background-color: transparent !important;
  color: #1890ff;
}
.login-layout .login-form .login-icon {
  color: #1890ff;
}
.login-layout .login-form .ant-input-password-icon {
  color: #1890ff;
}
